@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  .scrollbar-none {
    scrollbar-width: none;
  }
  .scrollbar-none::-webkit-scrollbar {
    display: none;
  }
}

.transition-max-height {
  transition: max-height 0.5s ease-in-out, opacity 0.3s ease-in-out;
}

.max-h-0 {
  max-height: 0;
  opacity: 0;
}

.max-h-[500px] {
  max-height: 500px;
  opacity: 1;
}

.hover-scale {
  transition: transform 0.3s ease-in-out;
}

.hover-scale:hover {
  transform: scale(1.02);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideIn {
  from {
    transform: translateX(-20px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.message-animation {
  animation: fadeIn 0.3s ease-out;
}

.suggestion-animation {
  animation: slideIn 0.2s ease-out;
}

.hover-scale {
  transition: transform 0.2s ease;
}

.hover-scale:hover {
  transform: scale(1.02);
}

.message-animation {
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hover-scale {
  transition: transform 0.2s ease;
}

.hover-scale:hover {
  transform: scale(1.01);
}

.accordion-content {
  transition: all 0.3s ease-in-out;
}

.accordion-icon {
  transition: transform 0.3s ease;
}

.skeleton-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #e2e8f0;
  animation: pulse 1.5s infinite;
  flex-shrink: 0;
}

.message-content.skeleton {
  background: transparent;
  padding: 0;
  width: 100%;
}

.skeleton-line {
  height: 16px;
  background-color: #e2e8f0;
  border-radius: 4px;
  margin-bottom: 8px;
  animation: pulse 1.5s infinite;
}

.skeleton-line:nth-child(1) {
  width: 90%;
}

.skeleton-line:nth-child(2) {
  width: 80%;
}

.skeleton-line:nth-child(3) {
  width: 60%;
}

@keyframes pulse {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 0.6;
  }
}

/* Ensure the skeleton animation matches your existing message animation */
.message-animation {
  animation: slideIn 0.3s ease-out;
}

.menu-container {
  position: relative;
  z-index: 50;
}
